<template>
  <!-- 添加汇率弹窗 -->
  <mrc_ess_myDialog :isopen='isopen'
                    @cancel='cancel'
                    @success='success'
                    :width='550'
                    :disabled='true'>
    <v-card-text>
      <v-row dense>
        <mrc_ess_switch :true-value="1"
                        :false-value="0"
                        v-model="params.is_used"
                        :true-text='$t("label_To_enable_the")'
                        :false-text='$t("label_disable")'></mrc_ess_switch>
        <v-col class="d-flex"
               cols="12"
               sm="12">
          <mrc_ess_prompt :rules="false"
                          :disabled='false'
                          :label="$t('label_The_original_money')+'*'"
                          :chips='false'
                          :errMsg="$errors('params','original_currency_uuid',requiredRule)"
                          v-model="params.original_currency_uuid"
                          :obj='company_name'>
          </mrc_ess_prompt>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="12">
          <mrc_ess_prompt :rules="false"
                          :disabled='false'
                          :label="$t('label_Converted_into')+'*'"
                          :errMsg="$errors('params','foreign_currency_uuid',requiredRule)"
                          v-model="params.foreign_currency_uuid"
                          :chips='false'
                          :obj='company_name'>
          </mrc_ess_prompt>
        </v-col>
        <v-col class="d-flex"
               cols="12"
               sm="12">
          <mrc_ess_dictName :rules="false"
                            :disabled='false'
                            :errMsg="$errors('params','frequency',requiredRule)"
                            :label="$t('label_The_update_frequency')+'*'"
                            v-model="params.frequency"
                            :chips='false'
                            dict_name='frequency'></mrc_ess_dictName>
        </v-col>
      </v-row>
    </v-card-text>
  </mrc_ess_myDialog>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    isopen: {
      type: Boolean,
      default: false
    },
    paramsData: {
      type: Object,
      default: () => { }
    }
  },
  model: {
    prop: 'isopen',
    event: 'flag'
  },
  data () {
    return {
      status: true,
      params: {
        uuid: null,
        original_currency_uuid: '',
        foreign_currency_uuid: '',
        frequency: '',
        is_used: 1,
        org_level_uuid: this.$store.state.userData.org_level_uuid,
        // 临时数据 后面要删除
        exchange_rate: 1.5,
        exchanged_utc_datetime: `${new Date().valueOf()}`,
        exchange_time_zone: 1
      }
    };
  },
  validations: {
    params: {
      original_currency_uuid: {
        required
      },
      foreign_currency_uuid: {
        required
      },
      frequency: {
        required
      }
    }
  },
  created () { },
  mounted () { },
  methods: {
    success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.$emit('getData', this.params)
      this.$emit('flag', !this.isopen)
    },
    cancel () {
      this.$emit('flag', !this.isopen)
      this.$emit('cancel')
    }
  },
  computed: {
    company_name () {
      return { prompt_name: 'company_name', org_level_uuid: this.$store.state.uuid }
    }
  },
  watch: {
    paramsData: {
      handler (val) {
        console.log(val, 'val')
        if (Object.keys(val).length > 0) {
          this.params.uuid = val.uuid
          this.params.is_used = val.is_used
          this.params.original_currency_uuid = val.original_currency_uuid
          this.params.foreign_currency_uuid = val.foreign_currency_uuid
          this.params.frequency = val.frequency
        }
      },
      deep: true
    },
    isopen (val) {
      if (!val) {
        this.params.uuid = ''
        this.params.original_currency_uuid = ''
        this.params.foreign_currency_uuid = ''
        this.params.frequency = ''
      }
    }
  },
};
</script>

