<template>
  <v-card>
    <mrc_ess_skLoaders :skLoading='skLoading'>
      <div>
        <mrc_ess_iconGroup style="top: -50px"
                           :is_disabled="disabled"
                           :showEdit="hasBtn('edit-company-exchange-rate')"
                           :show_del="hasBtn('delete-company-exchange-rate')"
                           @handleReturn='handleReturn'
                           @handleEdit='handleEdit'
                           @handleDel='handleDel'></mrc_ess_iconGroup>
        <v-card>
          <v-card-text>
            <v-data-table :headers="headers"
                          :items="desserts"
                          class="elevation-1"
                          hide-default-footer>
              <template v-slot:item.frequency='{item}'>
                <div class="is_flex_between">
                  <span>{{item.frequency}}</span>
                  <div class="is_flex_column"
                       style="margin-right: 30%;color:#ff9100;cursor: pointer"
                       @click="updateRate(item.uuid)">
                    <span>{{$t('label_update_immediately')}}</span>
                    <v-divider color='#ff9100'></v-divider>
                  </div>
                </div>
              </template>
              <template v-slot:item.status='{ item }'>
                <div class="status"
                     :class="{'is_used': item.status==0}">{{item.status==1 ? $t('label_To_enable_the') : $t('label_disable')}}</div>
              </template>
              <template v-slot:item.actions='{item}'>
                <v-icon @click.stop="changeItem(item)">mdi-pencil</v-icon>
                <v-icon style="margin-left: 10%"
                        @click.stop="delItem(item)">mdi-delete</v-icon>
              </template>
            </v-data-table>
          </v-card-text>
          <div class="addRate"
               @click="flag = true">{{$t('label_The_new_currency')}}+</div>
          <rate-dialog v-model="flag"
                       @cancel='cancalDialog'
                       :paramsData='dialogData'
                       @getData='saveAddUpdate'></rate-dialog>
          <div style="width: 400px;float: right">
            <v-pagination v-model="cpage"
                          class="my-4"
                          :length="totalpage"
                          v-if='totalpage !== 0'
                          light
                          @input='turnPage'
                          color="primary"></v-pagination>
          </div>
        </v-card>
      </div>
    </mrc_ess_skLoaders>
  </v-card>
</template>

<script>
import rateDialog from '../components/addRate'
import { rate_list, rate_update_add, rate_del, rate_detail, rate_del_all } from '@/service/api/company_set'
export default {
  components: { rateDialog },
  props: {},
  data () {
    return {
      skLoading: true,
      disabled: true,
      flag: false,
      baseHeader: [
        {
          text: this.$t('label_The_original_money'),
          align: 'start',
          sortable: false,
          value: 'original_currency_uuid',
        },
        {
          text: this.$t('label_Converted_into'),
          align: 'start',
          sortable: false,
          value: 'foreign_currency_uuid',
        },
        {
          text: this.$t('label_The_latest_currency'),
          align: 'start',
          sortable: false,
          value: 'exchange_rate',
        },
        {
          text: this.$t('label_Inverse'),
          align: 'start',
          sortable: false,
          value: 'inverse',
        },
        {
          text: this.$t('label_The_update_frequency'),
          align: 'start',
          sortable: false,
          value: 'frequency',
        },
        {
          text: this.$t('label_state'),
          align: 'start',
          sortable: false,
          value: 'status',
        },
        {
          text: ' ',
          align: 'start',
          sortable: false,
          value: 'actions',
        }
      ],
      desserts: [
      ],
      dialogData: {},
      cpage: this.$store.state.cpage0,
      pagesize: 1,
      totalpage: 0
    };
  },
  created () { },
  async mounted () {
    if (!this.$store.state.iconstatus) {
      this.disabled = false
    }
    this.skLoading = true
    await this.getList()
    this.skLoading = false
  },
  methods: {
    async turnPage (num) {
      this.cpage = num
      await this.getList()
    },
    async getList () {
      const list = await rate_list({
        org_level_uuid: this.$store.state.uuid,
        cpage: this.cpage,
        pagesize: this.pagesize
      })
      if (list.code == 200) {
        this.desserts = list.data
        // console.log(list)
      }
    },
    // 立即更新
    updateRate (uuid) {
      console.log(uuid)
    },
    // 修改汇率 打开修改弹窗
    changeItem (item) {
      this.dialogData = item
      this.flag = true
      console.log(item)
    },
    // 删除这一条
    delItem (item) {
      this.$myconfirm({ title: '提示', content: '是否删除这一条' })
        .then(async () => {
          const index = this.desserts.indexOf(item)
          console.log(item, 'delete')
          const del = await rate_del({ uuid: this.$store.state.uuid })
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.desserts.splice(index, 1)
            await this.getList()
          }
        })
        .catch(() => { })

    },
    // 取消新增或者修改
    cancalDialog () {

    },
    // 弹窗修改或者新增
    async saveAddUpdate (params) {
      const data = await rate_update_add(params)
      if (data.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_submission_was_successful'))
        await this.getList()
        this.disabled = !this.disabled
      }
      this.flag = false
    },
    handleReturn () {
      this.$router.go(-1)
    },
    handleEdit () {
      this.disabled = !this.disabled
    },
    // 删除
    handleDel () {
      this.$myConfirm({ content: $t('message_Confirm_the_deletion') })
        .then(async () => {
          const del = await raterate_del_alldel({
            org_level_uuid: this.params.org_level_uuid
          })
          if (del.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
            this.$router.go(-1)
          }
        }).catch(() => { })
    },
  },
  computed: {
    headers () {
      return this.baseHeader
    }
  },
  watch: {
    // disabled(val) {
    //   if (val) {
    //     this.baseHeader.pop()
    //   } else {
    //     this.baseHeader.push({
    //       text: ' ',
    //       align: 'start',
    //       sortable: false,
    //       value: 'actions',
    //     })
    //   }
    // }
  },
};
</script>

<style scoped>
.addRate {
  text-align: left;
  margin: 0 10px 20px 20px;
  padding: 10px;
  min-width: 100px;
  position: relative;
  top: -5px;
  color: #ff9100;
  cursor: pointer;
}
</style>
